.techs__icons {
    padding: 0 0 100px 0;
    margin: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fill, 84px);
    max-width: 690px;
    width: 100%;
    gap: 16px;
}

@media screen and (max-width:950px) {
    .techs__icons {
        padding: 0 0 90px 0;
        max-width: 650px;
        gap: 10px;
    }
}

@media screen and (max-width:400px) {
    .techs__icons {
        max-width: 178px;
        padding: 0 71px 70px 71px;
    }
}