.portfolio__listitem {
    max-width: 1140px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 20px 0 0 0;
}

.portfolio__listitem:not(:last-child) {
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #E6E6E6;
}